'use strict';

export default class NewMediaService {

  /*@ngInject*/
  constructor(alertService, $log,$window) {
    this.alertService = alertService;
    this.$log = $log;
    this.invalidMedia = true;
    this.invalidMaterial = true;
    this.invalidPlusDetail = true;
    this.invalidSegment = true;
    this.window = $window;
    this.EDIT = {};
    this.tabsCtrl = [];
  }

  getTab(index) {
    return this.activeTab[index];
  }

  createTabSelection(numberOfTabs){
    this.activeTab = [];
    this.activeTab[0] = true;

    for(var i = 1; i < numberOfTabs; i++){
      this.activeTab[i] = false;
    }
    this.tabsCtrl = [];
  }

  nextStep(){

    var selectedTab = this._getActiveTab();

    if(selectedTab + 1 == this.activeTab.length){
      this.lastTab = true;
    } else {

      for(var i = 0 ; i < this.activeTab.length ; i++){
        this.activeTab[i] = false;
      }
      this.activeTab[selectedTab + 1] = true;
      this.lastTab = false;
    }
  }

  updateTabIndex(index){
    for(var i = 0 ; i < this.activeTab.length ; i++){
      this.activeTab[i] = false;
    }

    this.activeTab[index] = true;

    if(index + 1 == this.activeTab.length){
      this.lastTab = true;
    } else {
      this.lastTab = false;
    }

    this.tabsCtrl[index]._onTabLoad();
  }

  _getActiveTab(){

    var activeTab;

    for(var i = 0 ; i < this.activeTab.length ; i++){
      if(this.activeTab[i]){
        activeTab = i;
      }
    }
    return activeTab;
  }

  goToBackState(){
    this.model = {};
    if(this.mediaId){
      this.mediaId = undefined;
    }
    this.window.history.back();
  }

  getModel(){
    return this.model;
  }

  getFromModel(key){
    return this.model[key];
  }

  setToModel(key,value){
    this.model[key] = value;
  }

  cleanService(){
    this.mediaId = undefined;
    this.model = {};
    this.invalidMaterial = true;
    this.invalidPlusDetail = true;
    this.invalidSegment =  true;
  }

}
